.container{

    background-color:  yellow;

    position:absolute;
    z-index: 100;

    height:50vh;
    width: 40%;
    border-radius:10px;
    background-color: white;
    box-shadow: 0 2px 500px rgba(0,0,0, 50);
    top:150px;
    left: 50%;
    transform: translate(-50%, 0);

    display: flex;
    flex-direction: column;
    align-items: center;


}

.top{
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom: 2px solid rgb(224, 224, 224);
    height: 33px;
    width:90%;
    font-size:30px;
    text-align: center;
    text-justify: center;
    padding:20px;
    color:rgb(6, 29, 42);

}

.close{
    position:absolute;
    top:27px;
    right:27px;
    height: 15px;
    width: 15px;
    opacity: 50%;
    cursor: pointer;
}
.close:hover{
    opacity: 100%;
}


.list{
    display: inline;
    overflow: scroll;
    z-index: 100;

    height: 100%;
    width: 100%;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}

@media(max-width: 650px){
    .container{
        width:95%;


        top:50%;
        transform: translate(-50%, 0);
    }
}