.container{

    padding-top:50px;

    width: 100vw;
    background-color: white;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

}
.bottom{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;


    background-color: rgb(238, 238, 238);
    box-shadow: 0 0 20px 10px rgba(0,124,255, 1);

    background-color: rgb(0,124,255);
}





.top{
    height: 100%;
    width: 70%;
    display: flex;
    align-items: flex-start;
    justify-content:   center;

}



.box{
    height: 100%;
    width: 60%;
    margin: 10px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.NFTwrapper{
    width:40%;
    margin: 10px;
    margin-bottom: 50px;
}


.nftText{
    font-size: 30px;
    font-weight: bold;
    color: rgb(6, 29, 42);
}
.text{
    margin-top:20px;
    font-size: 20px;
}

.buttonWrapper{
    margin-top: 20px;
    display: flex;
    gap: 10px;

    height: 40px;

}


@media(max-width: 1100px) {

    .top{
        width: 80%;
    }
    
}
@media(max-width: 1000px) {

    .top{
        width: 90%;
    }
    
}
@media(max-width: 800px) {

    .top{
        width: 96%;
    }
    
}

@media(max-width: 700px) {
    .top{
        flex-direction: column;
        align-items: center;
    }
    .box{
        width: 70%;
    }
    .NFTwrapper{
        
        width:50%;
    }
}
@media(max-width: 600px) {
    .box{
        width: 90%;
    }
    .NFTwrapper{
        width:65%;
    }
}

